@import './Var.scss';

/*start button about this site*/

.container-btn-about-this {
	padding: 70px 0;
	text-align: center;
}

.btn-about-this {
	background-color: $blue;
	padding: 15px 70px;
	color: $white;
	display: inline-block;
	cursor: pointer;
}

.btn-about-this:after {
	content: "";
	background-color: $yellow;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	display: inline-block;
	position: relative;
	top: -10px;
	right: -5px;
}

.btn-about-this {
	overflow: hidden;
}

.btn-about-this {
	position: relative;
	transition: .75s;
}

.btn-about-this:after {
	transition: .75s;
	position: absolute;
	top: 15px;
	right: 55px;
}

.btn-about-this:hover:after {
	top: -17px;
	right: -17px;
	width: 35px;
	height: 35px;
}


/*end button about this site*/