@import './Var.scss';
body,
html,
h4,
h5 {
    padding: 0;
    margin: 0;
    height: 100%;
}

.medium-container {
    max-width: $large;
    margin: 0 auto;
}

.header-subtitle {
    font-size: 18px;
    color: $yellow;
}

.wrapper-about {}

.container-about {}

header.my-title {
    position: relative;
    z-index: 1;
    top: 7px;
    left: 20px;
    display: inline-block;
}

header.my-title h2 {
    padding: 0;
    margin: 0;
    color: $blue;
    display: inline-block;
    position: relative;
    font-size: 50px;
    top: 10px;
}

header.my-title h2:after {
    content: "";
    background-color: $yellow;
    width: 100%;
    position: absolute;
    left: 50%;
    height: 7px;
    bottom: -12px;
    transform: translate(-50%, 0);
    z-index: -1;
}