@import './Var.scss';

/*start css for overlay - about this site*/

.overlay-wrapper {
    display: none;
}

.opened-overlay .overlay-wrapper {
    display: block;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 200;
}

body.opened-overlay {
    overflow: hidden;
}

.bg-overlay {
    background-color: #193e6b;
    opacity: .7;
    width: 100%;
    height: 100%;
}

.container-overlay-big {
    padding: 20px;
    position: fixed;
    top: 50%;
    left: 50%;
    max-width: 550px;
    transform: translate(-50%, -50%);
    width: 100%;
    width: 100%;
    height: 100%;
}

.container-overlay {
    z-index: 300;
    text-align: center;
}

.container-overlay-inside {
    background-color: #efefef;
    margin: 20px 10px;
}

.header-overlay {
    color: #1a3c6a;
    font-size: 22px;
    font-weight: bold;
    text-align: center;
    padding: 20px 0;
}

.header-overlay:after {
    content: "";
    background-color: #ffc801;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    top: -10px;
    right: -5px;
}

.overlay-item {
    background-color: #fff;
    padding: 20px 20%;
    margin: 0 20px 20px 20px;
    border-radius: 8px;
}

.overlay-item {
    text-align: center;
}

.overlay-item-title {
    color: #000000;
    padding-bottom: 5px;
    font-weight: bold;
}

.overlay-item-title:after {
    content: "";
    display: inline-block;
    background-color: #d7d7d7;
    width: 100%;
    height: 2px;
}

.overlay-item-description {
    color: #707070;
    padding-bottom: 5px;
    display: inline-flex;
    align-items: center;
}

.color-for-the-site {
    width: 17px;
    height: 17px;
    display: inline-block;
    border-radius: 50%;
    margin: 0 10px;
}

.blue-color {
    background-color: $blue;
}

.yellow-color {
    background-color: $yellow;
}

.heavy-blue-color {
    background-color: $heavy-blue;
}

.grey-color {
    background-color: $heavy-grey;
}

.light-grey-color {
    background-color: $light-grey;
}

.close-overlay {
    text-align: center;
    cursor: pointer;
    color: $blue;
    font-size: 22px;
    text-decoration: underline;
    padding-bottom: 20px;
    display: inline-block;
    margin: 0 auto;
}

@media (max-width: $small) {
    .overlay-item {
        padding: 10px 20px;
    }
    .color-for-the-site {
        margin: 0 2px;
    }
    .overlay-item-description {
        width: 100%;
        justify-content: center;
    }
}

@media (max-height: 750px) {
    .container-overlay {
        z-index: 300;
        text-align: center;
        overflow-y: scroll;
        position: relative;
        height: 100%;
    }
}


/* width */

.container-overlay-big ::-webkit-scrollbar {
    width: 10px;
}


/* Track */

.container-overlay-big ::-webkit-scrollbar-track {
    background: $white-close;
    margin: 20px 0;
}


/* Handle */

.container-overlay-big ::-webkit-scrollbar-thumb {
    background: #888;
}


/* Handle on hover */

.container-overlay-big ::-webkit-scrollbar-thumb:hover {
    background: #555;
}


/*end css for overlay- about this site*/