@import './Var.scss';
 #loading {
     background-color: $loader-grey;
     height: 100%;
     padding: 0;
     position: fixed;
     top: 0px;
     left: 0px;
     width: 100%;
     z-index: 99999;
}
 #loader {
     position: relative;
     top: 30%;
     text-align: center;
     margin: 0 auto;
     width: 200px;
     height: 200px;
}
 .cycle_loader {
     position: absolute;
     top: 28px;
     left: 30px;
     border-radius: 50%;
     background-color: $black;
     width: 140px;
     height: 140px;
}
 .load_img0 {
     position: absolute;
     top: 50px;
     left: 75px;
}
 .load_img1 {
     position: absolute;
     top: 70px;
     left: 75px;
}
 .load_img2 {
     position: absolute;
     top: 70px;
     left: 101px;
}
 .stoploaderAnimation{
    margin:0 auto;
    text-align:center;
    font-size:30px;
    max-width:200px;
    width:100%;
    z-index: 999999;
    position:relative;
    color: $white;
    cursor:pointer;
}
 .stoploaderAnimation{
    transition:1s;
}