@import './Var.scss';

/*start for my work*/

.wrapper-my-work {
	background-color: $heavy-blue;
}

.container-my-work {
	padding: 70px 0 0;
}

.section_team.group {
	display: inline-flex;
	width: 100%;
	flex-wrap: wrap;
}

.col {
	display: block;
	float: left;
	margin: 0%;
}

.span_1_of_3 {
	position: relative;
	overflow: hidden;
	cursor: auto;
	width: 33.3%;
	background-color: $heavy-blue;
	color: white;
	height: auto;
}

.image_box {
	position: relative;
	display: block;
	max-width: 100%;
	width: 100%;
	bottom: 0px;
	-ms-transform: translateY(100px);
	/* IE 9 */
	-webkit-transform: translateY(100px);
	/* Safari */
	transform: translateY(100px);
	-webkit-transition: all .4s ease-out;
	transition: all .4s ease-out;
	bottom: 100px;
}

.span_1_of_3:hover .image_box {
	bottom: 200px;
}

.keimena_B {
	position: absolute;
	bottom: 0px;
	left: 0;
	width: 100%;
	z-index: 11111;
	text-align: left;
	opacity: 1;
	-ms-transform: translateY(100px);
	/* IE 9 */
	-webkit-transform: translateY(100px);
	/* Safari */
	transform: translateY(100px);
	-webkit-transition: all .4s ease-out;
	transition: all .4s ease-out;
	height: 100px;
	background-color: #193e6b;
	z-index: 0;
}

.keimeno1_B {
	z-index: 11111;
	color: $white;
	font-size: 20px;
	width: 100%;
	font-weight: 700;
	margin-left: 20px;
	margin-bottom: 10px;
	margin-top: 20px;
}

.keimeno2_B {
	z-index: 11111;
	color: $white;
	font-size: 18px;
	width: 100%;
	font-weight: 500;
	margin-left: 20px;
	font-weight: 400;
}

.span_1_of_3:hover .keimena_B {
	bottom: 100px;
}

header.my-title {
	position: relative;
	z-index: 1;
	top: 7px;
	left: 20px;
	display: inline-block;
}

header.my-title h2 {
	padding: 0;
	margin: 0;
	color: $blue;
	display: inline-block;
	position: relative;
}

header.my-title h2:after {
	content: "";
	background-color: $yellow;
	width: 100%;
	position: absolute;
	left: 50%;
	height: 7px;
	bottom: 12px;
	transform: translate(-50%, 0);
	z-index: -1;
}

.wrapper-brands .header-subtitle {
	padding-left: 10px;
}


/*end for my work*/