@import './Var.scss';
/*experience education start*/

.container-experience-education {
    padding: 50px 15px;
    text-align: center;
    background-color: $light-grey;
}

.row-experience-education {
    display: inline-flex;
    width: 100%;
    flex-wrap: nowrap;
}

.white-box {
    display: inline-flex;
    position: relative;
    flex-wrap: nowrap;
    width: 100%;
    align-items: center;
}

.white-box-left img {
    max-width: 100px;
    padding: 25px 20px;
}

.col-experience-education-left,
.col-experience-education-right {
    width: 50%;
    background: $white;
    box-shadow: 0px 0px 5px 0px rgba(167, 161, 161, 0.75);
    position: relative;
    overflow:hidden;
    padding: 10px 0;
}

.white-box-right h4 {
    color: $blue;
    font-weight: bold;
    font-size: 22px;
    text-align: left;
    padding-left: 20px;
}

.white-box-right h5 {
    color: $blue;
    font-weight: normal;
    font-size: 22px;
    text-align: left;
    padding-left: 20px;
}

.col-experience-education-right .white-box-right h5,
.col-experience-education-right .white-box-right h4 {
    text-align: right;
}

.col-experience-education-left {
    margin-right: 10px;
}

.col-experience-education-right {
    margin-left: 10px;
}

.shape-box {
    position: absolute;
    left:10%;top:0;transform: scale(.85) rotate(0deg)
    }
.shape-box.animate-box{
    -webkit-animation-name: shape-one; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
    animation-name: shape-one;
    animation-duration: 1s;
    animation-iteration-count: forwards;animation-timing-function: linear;}
@-webkit-keyframes shape-one {
  0% {margin-left:0;margin-top:0;transform: scale(.85) rotate(0deg)}
  50% {margin-left:-20px;margin-top:-20px;transform: scale(.45) rotate(14deg)}
  100% {margin-left:0;margin-top:0;transform: scale(.85) rotate(0deg)}
}

/* Standard syntax */
@keyframes shape-one {
  0% {margin-left:0;margin-top:0;transform: scale(.85) rotate(0deg)}
  50% {margin-left:-20px;margin-top:-20px;transform: scale(.45) rotate(14deg)}
  100% {margin-left:0;margin-top:0;transform: scale(.85) rotate(0deg)}
}


.col-experience-education-right .shape-box {
    position: absolute;
    right: 10%;
    left: auto;
    }
/*experience education end*/