@import './Var.scss';
.home-texts{
    margin-bottom: 140px;
}
.home-texts-row {
    display: inline-flex;
    flex-wrap: nowrap;
    width:100%;
}
.home-texts-left{
    position: relative;
    width:300px;
    height: 300px;
    z-index:1;
}
.home-texts-right{
    width:240px;
}
.hello-text-mirror,.hello-text-back{
    position: absolute;
    bottom:0;
    right:0;
    opacity:.4;

}
.hello-text-front{
    position: absolute;
    bottom:0;
    right:0;

}
.home-texts-right {
    display: flex;
    align-items: flex-end;
}
.home-texts-right h1{
    padding:0;
    margin:0;
}
.home-texts-right h1 span{
    display: block;
    font-size: 50px;
    line-height: 55px;
    font-weight: bold;
}
.hello-text-mirror,.hello-text-back, .hello-text-front{
    opacity:0;
}
.home-texts-right header{
}
.second-home-row{
    margin-left: 300px;
     width: calc(100% - 300px);
     color: $blackc;
     font-size: 28px;
}
.professional{
    color: $white;
    background-color: $blue;
    padding: 2px;
    border-radius: 2px;
    margin-left: 10px;
}
.hello-name{display:inline-flex !important;}