@import './Var.scss';
@media (max-width: $medium) {
	.row-first-about {
		flex-wrap: wrap
	}
	.col-first-about-left,
	.col-first-about-right {
		width: 100%;
	}
	.col-first-about-left-img img {
		max-width: 350px;
	}
	.white-box-left img {
		max-width: 75px;
	}
	.row-experience-education {
		flex-wrap: wrap;
	}
	.col-experience-education-left,
	.col-experience-education-right {
		width: 100%;
		margin: 10px 0;
	}
	.col-experience-education-right .shape-box {
		left: 10%;
		right: auto;
	}
	.white-box-left {
		order: 1;
	}
	.white-box-right {
		order: 2;
	}
	.col-experience-education-right .white-box-right h5,
	.col-experience-education-right .white-box-right h4 {
		text-align: left;
		padding-left: 5px;
	}
}
