@import './Var.scss';

/*skills start*/

.container-skills {
	background-color: $blue;
	text-align: center;
}

.row-skills {
	display: inline-flex;
	width: 100%;
	flex-wrap: nowrap;
	padding-top: 50px;
	padding-bottom: 50px;
}

.col-skills-left {
	width: 100px;
}

.col-skills-left img {
	width: 100%;
	height: auto;
	max-width: 70px;
}

.col-skills-right {
	text-align: left;
}

.skills-list {
	margin-left: 0;
	padding: 0;
}

.skills-list li {
	list-style-type: none;
	margin-left: 0;
	padding: 5px 0;
	color: $white;
}

.col-skills-right {
	padding-left: 20px;
}


/*skills end*/