@import './Var.scss';
.container-homepage {
    width: 100%;
    padding: 0;
    margin: 0;
    height: 100%;
}

.row-homepage {
    display: inline-flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.side-homepage {
    width: 50%;
    height: 100%;
}

.left-side-homepage {
    background-color: $light-grey;
    display: inline-flex;
    justify-content: flex-end;
}

.right-side-homepage {
    background-color: $blue;
}

.right-side-homepage {
    position: relative;
}

.hello-letter,
.hello-surname {
    opacity: 0;
}

.second-home-row {
    opacity: 0;
}

.img-mask {
    position: absolute;
    top: 0;
    left: -1px;
    height: 100%;
    width: 18%;
}

.img-triangle-yellow {
    position: absolute;
    height: 20%;
    width: 20%;
    left: 7%;
    top: 10%;
}

.img-gravata {
    position: absolute;
    height: auto;
    width: 12%;
    left: -1%;
    top: 44%;
}

.img-gravata-kyklos {
    position: absolute;
    height: auto;
    width: 19%;
    left: -4.7%;
    top: 40.5%;
}

.content-homepage {
    background-color: $white;
    width: 100%;
    max-width: 700px;
    /*if basic container is 1400px this has div has the half width*/
}

@media (max-width: $small) {
    .row-homepage {
        display: inline-flex;
        flex-direction: column-reverse;
        flex-wrap: nowrap;
    }
    .side-homepage {
        width: 100%;
    }
    .right-side-homepage {
        height: 150px;
        overflow: hidden;
    }
    .left-side-homepage {
        height: calc(100%-150px);
    }
}

.content-homepage {
    position: relative;
    display: flex;
    align-items: center;
}

.img-resp.zogkler-reload:hover {
    background-color: $light-blue;
}

.hide-desktop {
    display: none;
}

.hide-mobile {
    display: block;
}

@media (max-width:$large) and (min-width:$smallplusone) {
    .img-triangle-yellow {
        position: absolute;
        height: auto;
        width: 88px;
    }
    .img-gravata {
        position: absolute;
        height: auto;
        width: 50px;
        left: 8px;
        top: 44%;
    }
    .img-gravata-kyklos {
        position: absolute;
        height: auto;
        width: 80px;
        left: -8px;
        top: 42%;
    }
    .second-home-row {
        width: 100%;
        color: $blackc;
        font-size: 28px;
        margin: 30px auto 0;
        text-align: center;
        display: inline-block !important;
    }
    .hello-text-mirror img,
    .hello-text-back img,
    .hello-text-front img {
        display: inline-block;
    }
    .hello-text-mirror img,
    .hello-text-back img {
        max-height: 200px;
        width: auto;
    }
    .hello-text-front img {
        max-height: 100px;
        width: auto;
    }
    .home-texts-left {
        width: 40%;
        height: auto;
    }
    .home-texts-right {
        width: 60%;
        height: auto;
    }
    .home-texts {
        display: block;
        margin: 50px auto 100px;
    }
    .hello-text-back {
        display: none;
    }
    .hello-text-front {
        right: 20px;
    }
    .content-homepage {
        display: inline-block;
    }
    .home-texts {
        display: block;
        margin: 50px auto 100px;
        position: absolute;
        margin: 0 auto;
        transform: translate(-50%, -50%);
        left: 50%;
        top: 50%;
        width: 100%;
    }
}

@media (max-width:768px) {
    .hide-desktop {
        display: block;
    }
    .hide-mobile {
        display: none;
    }
    .white-bg-home {
        width: 100%;
        height: auto;
        bottom: 0px;
        top: auto;
        left: 0;
    }
    .img-triangle-yellow {
        height: auto;
        width: 11%;
        left: 38%;
        top: auto;
        bottom: 10px;
    }
    .img-gravata {
        height: auto;
        width: 51px;
        left: 27px;
        top: auto;
        bottom: 19px;
    }
    .img-gravata-kyklos {
        height: auto;
        width: 75px;
        left: 15px;
        top: auto;
        bottom: 8px;
    }
    .second-home-row {
        width: 100%;
        color: #333333;
        font-size: 28px;
        margin: 30px auto 0;
        text-align: center;
        display: inline-block !important;
    }
    .hello-text-mirror img,
    .hello-text-back img,
    .hello-text-front img {
        display: inline-block;
    }
    .hello-text-mirror img,
    .hello-text-back img {
        max-height: 200px;
        width: auto;
    }
    .hello-text-front img {
        max-height: 100px;
        width: auto;
    }
    .home-texts-left {
        width: 40%;
        height: auto;
    }
    .home-texts-right {
        width: 60%;
        height: auto;
    }
    .home-texts {
        display: block;
        margin: 80px auto 80px;
    }
    .hello-text-back {
        display: none;
    }
    .hello-text-front {
        right: 20px;
    }
    .content-homepage {
        display: inline-block;
    }
}

@media (max-height:600px) {
    .home-texts {
        margin: 20px auto;
    }
    .second-home-row {
        margin-top: 10px;
    }
}

@media (max-height: 500px) {
    .zogkler-container {
        display: none;
    }
}

.template-wrapper,
.template-wrapper section,
#root {
    height: 100%;
}

@media (max-width: $small) {
    .left-side-homepage {
        background-color: $white;
    }
}