@import './Var.scss';
/*start brands*/

.wrapper-brands {
	width: 100%;
	background-color: $heavy-blue;
}

.container-brands {
	padding: 70px 0;
}

.row-brands {
	display: inline-flex;
	width: 100%;
	flex-wrap: wrap;
}

.item-brand {
	width: 20%;
	padding: 30px 0;
	text-align: center;
}

@media (max-width:$large) {
	.item-brand {
		width: 25%;
	}
}

@media (max-width:$medium) {
	.item-brand {
		width: 33.3%;
	}
}

@media (max-width:$small) {
	.item-brand {
		width: 50%;
	}
}


/*end brands*/