@import './Var.scss';

/*start footer*/

.wrapper-footer {
    width: 100%;
    background-color: $light-grey;
}

.container-footer {
    padding: 70px 0;
    text-align: center;
}

.row-footer {
    display: inline-flex;
    width: 90%;
    flex-wrap: wrap;
}

.row-footer-right {
    width: 10%;
}

.row-footer-right img {
    max-width: 170px;
    height: auto;
    width: 100%;
}

.col-footer {
    width: 20%;
}

.footer-btn {
    background-color: $white;
    color: $blue;
    font-size: 18px;
    padding: 10px 20px;
    font-weight: bold;
    box-shadow: 0px 0px 5px 0px rgba(167, 161, 161, 0.75);
    width: 70%;
    display: inline-block;
}

.footer-logo-container {
    display: inline-flex;
    align-items: center;
    padding: 0 0 0 10px;
}

.logo-footer:after {
    content: "";
    width: 2px;
    height: 35px;
    background-color: $heavy-grey;
    display: inline-block;
    margin: 0 10px;
    margin-bottom: 3px;
}

.developed-and-designed-by-me-footer {
    color: $blackb;
}

.developed-and-designed-by-me-footer a {
    color: $yellow;
    text-decoration: underline;
}

.footer-two-rows {
    width: 100%;
    display: inline-flex;
    flex-wrap: nowrap;
}

.footer-logo-container {
    margin: 0;
}

.empty-row-footer {
    display: inline-flex;
    width: 100%;
    flex-wrap: nowrap;
    padding: 10px 0;
    justify-content: center;
    color: $blue;
    font-size: 18px;
    font-weight: bold;
}

@media(max-width: $medium) {
    .footer-two-rows {
        flex-wrap: wrap;
    }
    .row-footer {
        width: 100%;
    }
    .row-footer-right {
        width: 100%;
    }
    .footer-logo-container {
        justify-content: center;
        padding: 0px 0 20px;
    }
    .row-footer-right img {
        max-width: 100px;
    }
    .col-footer {
        width: 50%;
        margin: 10px 0
    }
    .container-footer {
        padding: 30px 0;
    }
}


/*footer end*/

.row-footer {
    width: 40%;
}

.row-footer.footer-logo-container {
    width: 30%;
}

.row-footer-right {
    width: 20%;
}

.footer-btn {
    border-radius: 3px;
    background-color: $blue;
    width: 15px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.row-footer-right img {
    max-width: 85px;
}

.col-footer {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-two-rows {
    width: 100%;
    display: inline-flex;
    flex-wrap: nowrap;
    align-items: center;
}

.row-footer-socials {
    align-items: center;
    justify-content: center;
}

@media (max-width: $large) {
    .row-footer-socials {
        width: 50%;
    }
    .row-footer.footer-logo-container {
        width: 49%;
        align-items: center;
        justify-content: center;
    }
    .row-footer-right {
        width: 100%;
    }
    .footer-two-rows {
        flex-wrap: wrap;
    }
    .row-footer-right {
        order: 3;
        display: none;
    }
}

@media (max-width:768px) {
    .row-footer-socials {
        width: 100%;
        order: 1;
    }
    .row-footer.footer-logo-container {
        width: 100%;
        align-items: center;
        order: 2;
    }
    .col-footer {
        justify-content: center;
    }
    .row-footer-right {
        order: 3;
        display: none;
    }
}

.footer-btn {
    position: relative;
    overflow: hidden;
    cursor: pointer;
}

.footer-btn:before {
    content: "";
    background-color: $blue-footerb;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
    transition: .35s;
    width: 0;
    height: 0;
    border-radius: 50%;
    border-top: 2px solid transparent;
    border-right: 2px solid transparent;
}

.footer-btn:hover:before {
    width: 200%;
    height: 200%;
    border-top: 2px solid $yellow;
    border-right: 2px solid $yellow;
}

.footer-btn img {
    z-index: 1;
}

.particles-js2 {
    position: absolute;
    max-height: 250px;
    width: 100%;
}

@media (max-width: $small) {
    .particles-js2 {
        display: none;
    }
}