@import "./Var.scss";

/*menu mobile here starts*/

.burger-container {
    display: none;
    cursor: pointer;
}

.my-menu-wrapper {
    width: 75%;
    height: 100%;
    position: fixed;
    background-color: $blue;
    z-index: 100;
    left: -100%;
    transition: .35s;
}

@media (max-width: $small) {
    .opened-menu + nav .my-menu-wrapper {
        left: 0;
        transition: .35s;
    }
    .my-menu-wrapper ul {
        padding-left: 0;
        height: 50%;
    }
    .my-menu-wrapper ul li {
        width: 100%;
        text-align: center;
        color: $white;
        height: 20%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }
    .opened-menu + nav .my-menu-wrapper ul li div {
        cursor: pointer;
        display: inline-block;
        position: relative;
    }
    .opened-menu + nav .my-menu-wrapper ul li.active div:after {
        content: "";
        background-color: $yellow;
        width: 71%;
        position: absolute;
        left: 50%;
        height: 3px;
        bottom: 0px;
        transform: translate(-50%, 0);
        z-index: -1;
    }
    .burger-container {
        display: inline-flex;
        position: fixed;
        right: 10px;
        top: 10px;
        transform: rotate(90deg);
        z-index: 1;
    }
    .burder-item {
        color: $yellow;
        font-size: 35px;
        font-weight: bold;
        transition: .35s;
        transition-timing-function: linear;
        transform-origin: top;
        padding: 0;
        position: relative;
        left: -5px;
    }
    .opened-menu .second-burger-item {
        opacity: 0;
    }
    .opened-menu .first-burger-item {
        transform: rotate(-45deg);
        position: relative;
        left: -15px;
        top: 0px;
    }
    .opened-menu .third-burger-item {
        transform: rotate(45deg);
        position: relative;
        left: 11px;
        top: 0px;
    }
}


/*menu mobile here end*/