@import './Var.scss';
.section_personal_projects {
	display: inline-flex;
	width: 100%;
	flex-wrap: wrap;
}

.personal_project-item {
	display: inline-flex;
	width: 50%;
}

.personal_project-item.no-sides {
	width: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.personal_project-item.no-sides .no-sides-content {
	display: inline-block;
	width: 100%;
	text-align: center;
}

.personal_project-item.no-sides img {
	width: 80%;
	height: auto;
	max-width: 550px;
}

.no-sides-content {
	padding: 30px 0;
}

.personal_project-item-left {
	width: 30%;
}

.personal_project-item-right {
	width: 70%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px 0;
}

.url-for-the-job {
	width: 80%;
	text-align: right;
	padding-top: 20px;
	max-width: 550px;
}

.url-for-the-job a {
	text-decoration: underline;
	color: $white;
	font-size: 18px;
}

.personal_project-item .personal_project-item-left {
	background-image: linear-gradient(to bottom, #ffcd08, #fda003);
}

.personal_project-item-right img {
	width: 80%;
}

.personal_project-item-left {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	padding: 0 20px;
}

@media (max-width: $medium) {
	.span_1_of_3 {
		width: 50%;
	}
	.personal_project-item.no-sides {
		width: 100%;
	}
	.personal_project-item {
		width: 100%;
	}
}

@media (max-width:768px) {
	.span_1_of_3 {
		width: 100%;
	}
}