@import './Var.scss';
@media (min-width: $smallplusone) {
    nav {
        /* align-content: center; */
        position: absolute;
        width: 50%;
        z-index: 1;
        text-align: center;
        right: 50%;
        max-width: 700px;
    }
    .my-menu-wrapper {
        width: 100%;
        position: relative;
        background-color: transparent;
        z-index: 9;
        left: 0;
        transition: .35s;
        max-width: 700px;
    }
    .my-menu-wrapper ul {
        display: inline-flex;
        flex-wrap: nowrap;
        width: 100%;
        list-style-type: none;
    }
    .my-menu-wrapper ul li {
        width: 25%;
        color: $heavy-grey;
        font-size: 24px;
    }
    .my-menu-wrapper ul li div {
        color: $heavy-blue;
        font-size: 24px;
        cursor: pointer;
        position: relative;
        display: inline-block;
    }
    .my-menu-wrapper ul li.active div:after {
        content: "";
        background-color: $yellow;
        width: 70%;
        position: absolute;
        left: 50%;
        height: 3px;
        bottom: 0px;
        transform: translate(-50%, 0);
        z-index: 0;
    }
    .my-menu-wrapper ul li div:after {
        content: "";
        background-color: $yellow;
        width: 0%;
        position: absolute;
        left: 50%;
        height: 3px;
        bottom: 0px;
        transition: .35s;
        transform: translate(-50%, 0);
        z-index: -1;
    }
    .my-menu-wrapper ul li div:hover:after {
        width: 70%;
        z-index: 0;
    }
}

.my-menu-wrapper.sticky {
    width: 100%;
    max-width: 100%;
    position: fixed;
    right: 0;
    height: 43px;
}

.my-menu-wrapper.sticky:before {
    content: "";
    background-color: $white;
    width: 100%;
    height: 43px;
    left: 0;
    position: absolute;
    opacity: 1;
}

.my-menu-wrapper.sticky ul {
    margin: 0 auto;
    max-width: 700px
}