@import './Var.scss';
.zogkler-container {
    position: relative;
    display: block;
    width: 100%;
    max-width: 350px;
}

.zogkler-preview {
    display: block;
    width: 100%;
    opacity: 0;
}

.img-resp {
    text-decoration: none;
    display: block;
    position: absolute;
    border: 0px;
}

.zogkler-table {
    width: 100%;
    bottom: 0.5%;
    left: 0%;
    height: auto;
}

.zogkler-body {
    width: 9%;
    bottom: 33%;
    left: 45%;
    height: auto;
}

.zogkler-left {
    width: 9%;
    bottom: 36%;
    left: 37.4%;
    height: auto;
}

.zogkler-right {
    width: 9%;
    bottom: 36%;
    left: 53%;
    height: auto;
}

.zogkler-hat {
    width: 15%;
    bottom: 30%;
    left: 7%;
    height: auto;
}

.zogkler-item-1,
.zogkler-item-2,
.zogkler-item-3,
.zogkler-item-4 {
    width: 10%;
    bottom: 40%;
    left: 33%;
    height: auto;
}

.img-resp.zogkler-reload:after {
    content: "";
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 10px solid white;
}

.zogkler-reload {
    font-size: 0;
    background-color: $blue;
    border-radius: 5px;
    height: auto;
    position: absolute;
    bottom: 31%;
    right: 7%;
    color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 4px 8px 5px 10px;
}

.zogkler-container {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}